import { Form } from './Form';
// @todo сделать оплату
export class OrderPayment extends Form {
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.events = events;
        this._address = container.elements.namedItem('address');
        this.buttonOnline = container.elements.namedItem('card');
        this.buttonCash = container.elements.namedItem('cash');
        if (this.buttonOnline) {
            this.buttonOnline.addEventListener('click', () => {
                events.emit(`order:changed`, {
                    payment: this.buttonOnline.name,
                    button: this.buttonOnline,
                });
            });
        }
        if (this.buttonCash) {
            this.buttonCash.addEventListener('click', () => {
                events.emit(`order:changed`, {
                    payment: this.buttonCash.name,
                    button: this.buttonCash,
                });
            });
        }
    }
    set address(value) {
        this._address.value = value;
    }
    togglePayment(value) {
        this.clearPayment();
        this.toggleClass(value, 'button_alt-active', true);
    }
    clearPayment() {
        this.toggleClass(this.buttonCash, 'button_alt-active', false);
        this.toggleClass(this.buttonOnline, 'button_alt-active', false);
    }
}
export class OrderContacts extends Form {
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.events = events;
        this._email = container.elements.namedItem('email');
        this._phone = container.elements.namedItem('phone');
    }
    set email(value) {
        this._email.value = value;
    }
    set phone(value) {
        this._phone.value = value;
    }
}
